import Noticia from '../components/Noticia'

const Noticias = (props) => {

   // console.log(props)

    const noticiasID = Object.keys(props.noticias);
    console.log('noticia' + noticiasID)

    return (
        <div className="row">
            {props.noticias.map(noticia => (
                <Noticia
                    key={'id'+noticiasID}
                    noticia={noticia}
                />
            ))}
        </div>
    );
};

export default Noticias;
